// 

export class CoverItem {
  imageURL: string; // 
  title: string; // 
  path: string; // 
  nextPath: string; //  state

  constructor(imageURL: string,
              title: string,
              path: string,
              nextPath: string) {
    this.imageURL = imageURL;
    this.title = title;
    this.path = path;
    this.nextPath = nextPath;
  }

}



