// 

import {CustomConfig} from 'app/core/bases/custom-config';
import {CoverItem} from 'app/core/objects/cover-item';
import {CoverItemLibrary} from 'app/core/libs/cover-item-library';
import {CardLibrary} from 'app/core/libs/card-library';
import {PayLibrary} from 'app/core/libs/pay-library';
import {PathLibrary} from 'app/core/libs/path-library';

export class SqsrmyyCustomConfig implements CustomConfig {

  private static readonly titleOutpatientPayment = '';

  //  icon 
  getIconURL(): string {
    return PathLibrary.iconPath_SQRY;
  }

  // 
  getModuleNamePrefix(): string {
    return PathLibrary.moduleName_SQRY;
  }

  //  item 
  getMenuItemData(): Array<CoverItem> {
    const coverData: CoverItem[] = [];

    coverData.push(new CoverItem(
      CoverItemLibrary.iconOutpatientPayment,
      SqsrmyyCustomConfig.titleOutpatientPayment,
      PathLibrary.readBarcode,
      this.getModuleNamePrefix() + '/' + PathLibrary.outpatientBilling
    ));

    return coverData;
  }

  // 
  getCardType(): string {
    return CardLibrary.m1Card; // M1
  }

  // 
  getOutpatientPayType(): string {
    return PayLibrary.dataSrcHIS;
  }

  // 
  getInpatientPayType(): string {
    return null;
  }

  //  item 
  showTimeDetailInOutpatientBillingItem(): boolean {
    return false;
  }

  // 
  getPayType(): Array<string> {
    return [PayLibrary.payTypeWeChatQRCode];
  }

}



