// 

import {CustomConfig} from 'app/core/bases/custom-config';
import {CoverItem} from 'app/core/objects/cover-item';
import {CoverItemLibrary} from 'app/core/libs/cover-item-library';
import {CardLibrary} from 'app/core/libs/card-library';
import {PayLibrary} from 'app/core/libs/pay-library';
import {PathLibrary} from 'app/core/libs/path-library';

export class AqsdermyyCustomConfig implements CustomConfig {

  private static readonly titleOutpatientPayment = '';
  private static readonly titleInpatientPayment = '';
  private static readonly titleEcardRecharge = '';

  //  icon 
  getIconURL(): string {
    return PathLibrary.iconPath_AQEY;
  }

  // 
  getModuleNamePrefix(): string {
    return PathLibrary.moduleName_AQEY;
  }

  //  item 
  getMenuItemData(): Array<CoverItem> {
    const coverData: CoverItem[] = [];

    coverData.push(new CoverItem(
      CoverItemLibrary.iconOutpatientPaymentQuery,
      AqsdermyyCustomConfig.titleOutpatientPayment,
      PathLibrary.readCardMagnetic,
      this.getModuleNamePrefix() + '/' + PathLibrary.outpatientBilling
    ));
    coverData.push(new CoverItem(
      CoverItemLibrary.iconInpatientPayment,
      AqsdermyyCustomConfig.titleInpatientPayment,
      PathLibrary.inpatientLogin,
      null
    ));
    coverData.push(new CoverItem(
      CoverItemLibrary.iconEcardRecharge,
      AqsdermyyCustomConfig.titleEcardRecharge,
      PathLibrary.paymentAmountSelect,
      null
    ));

    return coverData;
  }

  // 
  getCardType(): string {
    return CardLibrary.magneticCard; // 
  }

// 
  getOutpatientPayType(): string {
    return PayLibrary.dataSrcOneCardPay;
  }

  // 
  getInpatientPayType(): string {
    return PayLibrary.dataSrcInpatient;
  }

  //  item 
  showTimeDetailInOutpatientBillingItem(): boolean {
    return true;
  }

  // 
  getPayType(): Array<string> {
    return [PayLibrary.payTypeWeChatQRCode, PayLibrary.payTypeALiPayQRCode];
  }

}



